.row {
  margin-left: 20px;
}

.row__posters {
  display: flex;
  width: max-content;
}

.xscroll {
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 20px;
}

.xscroll::-webkit-scrollbar {
  display: none;
}

.row__poster {
  width: 100%;
  object-fit: contain;
  max-height: 200px;
  transition: transform 450ms;
  margin-right: 10px;
}

.row__poster:hover {
  transform: scale(1.08);
}

.row__posterLarge {
  max-height: 250px;
}

.row__posterLarge:hover {
  transform: scale(1.09);
  opacity: 1;
}
